body {
  background-color: #2C2F33;
  color: #FFFFFF;
  font-family: 'Roboto', sans-serif;
  /*  */
  background-image: url(/static/media/background.7cc561f6.png);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-size: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

.loading {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: url(/static/media/loader.f85536f6.gif) center no-repeat #fff;
}

.d-none {
  display: none;
}

.icons {
  vertical-align: middle;
}
.navbar {
    color: #fff;
    height: 77px;
    line-height: 77px;
    font-weight: 600;
    transition-duration: 0.5s;
    width: 100%;
}

/* NavBar Logo */
.navbar .navbar-brand {
    float: left;
    font-size: 20px;
}

.navbar h1 {
    margin: 0;
    font-size: 20px;
}

/* Mobile View Toggle Button */
.toggle {
    float: right;
    display: none;
    font-size: 25px;
    cursor: pointer;
}

/* NavBar Options */
.navbar-nav {
    height: inherit;
    display: -webkit-flex;
    display: flex;
    float: right;
    font-size: 15px;
    transition: all 0.1s ease-out;
}

.navbar-nav .nav-item {
    padding: 0px 30px;
    height: auto;
}

.navbar-nav .nav-item:hover {
    color: #a8a8a8;
}

.navbar-nav .nav-item .joindiscord {
    background-color: #7289DA;
    padding: 10px 20px;
    border-radius: 25px;
}

@media (max-width: 992px) {

    .navbar .navbar-nav {
        -webkit-flex-direction: column;
                flex-direction: column;
        margin: 0;
        position: absolute;
        top: 77px;
        left: -100%;
        width: 100%;
        text-align: center;
        background-color: #2C2F33;
        z-index: 1;
    }

    .navbar .toggle {
        display: block;
    }

    .navbar .show {
        left: 0%;
    }
}
.home {
    height: calc(100vh - 77px - 77px);
    text-align: center;

}

.home .col-12 {
    margin-top: auto;
    margin-bottom: auto;
}

.home .title {
    font-size: 6rem;
    text-transform: uppercase;
    font-weight: 600;
    color: #7289DA;
}

.home .subtitle {
    font-size: 2rem;
    margin: 10px 0px;
}

.home .joinbtn {
    background-color: #7289DA;
    font-size: 2rem;
    border-radius: 10px;
    margin: 10px 0px;
    padding: 20px;
    font-weight: 500;
    display: inline-block;
    box-shadow: 0px 0px 50px 5px rgba(114, 136, 218, 0.55);
    text-transform: uppercase;
}

@media (max-width: 992px) {

    .home .title {
        font-size: 4rem;
    }

    .home .subtitle {
        font-size: 1rem;
    }

}
.about {
    height: calc(100vh - 77px);
}

.about .col-12 {
    margin-top: auto;
    margin-bottom: auto;
}

.aboutcard {
    background-color: #7289DA;
    text-align: center;
}

.aboutcard img {
    width: 100%;
}

.aboutcard .name {
    font-size: 24px;
    padding: 10px 0px;
}

.aboutcard .socialmedia {
    padding: 10px 0px;
    font-size: 24px;
}

.aboutcard .socialmedia a {
    padding: 0px 10px;
}

@media (max-width: 992px) {

    .about {
        margin: 30px 0px;
    }

    .aboutcard {
        margin-bottom: 30px;
    }
}
.commandoptions {
    display: inline-block;
    padding: 10px 20px;
    font-size: 1.2rem;
    background-color: #292929;
    margin: 0px 10px 10px 0px;
    cursor: pointer;
    text-transform: capitalize;
}

.active {
    background-color: #7289DA;
}

.commands .command {
    background-color: #292929;
    padding: 20px 10px;
    font-size: 22px;
    margin: 30px 0px;
    border-radius: 5px;
}

.commands .command .description {
    font-size: 16px;
    color: #a5a5a5;
}

.title {
    font-size: 2rem;
    font-weight: 500;
    margin: 10px 0px;
}

.search {
    background-color: #292929;
    font-size: 1rem;
    color: #fff;
    border: none;
    margin-top: 30px;
    width: 100%;
    padding: 10px;
}

textarea:focus,
input:focus {
    outline: none;
}
